export function getBrightess(color) {
  // Convert the color to an array
  var rgb = color.replace(/[^\d,]/g, '').split(',');
  // Calculate the brightness
  var brightness = Math.round((parseInt(rgb[0]) * 299 + parseInt(rgb[1]) * 587 + parseInt(rgb[2]) * 114) / 1000);
  return brightness;
}
export function calculateColorContrast(color1, color2) {
  // Helper function to convert hex to RGB
  var hexToRGB = function hexToRGB(hex) {
    var r = parseInt(hex.substring(1, 3), 16);
    var g = parseInt(hex.substring(3, 5), 16);
    var b = parseInt(hex.substring(5, 7), 16);
    return {
      r: r,
      g: g,
      b: b
    };
  };

  // Helper function to calculate luminance
  var calculateLuminance = function calculateLuminance(rgb) {
    var r = rgb.r,
      g = rgb.g,
      b = rgb.b;
    var sRGB = [r, g, b].map(function (value) {
      value /= 255;
      return value <= 0.03928 ? value / 12.92 : Math.pow((value + 0.055) / 1.055, 2.4);
    });
    return 0.2126 * sRGB[0] + 0.7152 * sRGB[1] + 0.0722 * sRGB[2];
  };

  // Calculate color contrast ratio
  var rgb1 = hexToRGB(color1);
  var rgb2 = hexToRGB(color2);
  var luminance1 = calculateLuminance(rgb1);
  var luminance2 = calculateLuminance(rgb2);
  var contrastRatio = (Math.max(luminance1, luminance2) + 0.05) / (Math.min(luminance1, luminance2) + 0.05);

  // Determine accessibility score
  if (contrastRatio >= 7) {
    return "AAA";
  } else if (contrastRatio >= 4.5) {
    return "AA";
  } else if (contrastRatio >= 3) {
    return "AA Large";
  } else {
    return "Fail";
  }
}