      var textColour = example.value == 'dark' ? '#ffffff' : '#000000';
      // Get the background colour hex value
      var backgroundColour = '#' + input.value;
      // Get the score
      var score = calculateColorContrast(backgroundColour, textColour);
      // Set the background colour of the parent to the input's value
      parent.style.backgroundColor = backgroundColour;

